import axios from './axios';

// 获取产品名称
export function getProductName(option) {
  const url = '/weixin/product/lists';

  return axios.post(url, option);
}

// 获取产品列表
export function getProductList(option) {
  const url = '/weixin/product/investLists';

  return axios.post(url, option);
}

// 获取产品详情
export function getProductDetail(option) {
  const url = '/weixin/product/detail';

  return axios.post(url, option);
}

// 获取产品历史报告
export function getProductReport(option) {
  const url = '/weixin/product/reportLists';

  return axios.post(url, option);
}



